.headerBlock {
    background: #26A9E0;
    color: #FFFFFF;
    height: 95px;
    padding: 20px 0 0;
}
.headerContent {
    border: 1px solid#fff;
    padding: 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: center;
    width: 70%;
    margin: 0 auto;

}
.title {
    font-size: 50px;
}
.basket {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-width: 180px;
    cursor: pointer;/* 
    border: 1px solid green; */
    font-family: 'PT Sans',sans-serif;
}
.basket span {
    font-weight: 700;
}

@media only screen and (max-width : 768px) {
    /* .headerBlock {
        background: #000;
    } */
    .headerBlock img {
        display: none;
    }
    .basket span {
        border: 1px solid #fff;
        border-radius: 5px;
        padding: 5px 5px;
    }
    .basket {
        min-width: 120px;
    }
    .title {
        /* font-size: 1fr; */
    }
}


@media only screen and (max-width : 569px) {
    .headerContent {
        justify-content: center;
    }
    .headerBlock {
        height: 125px;
    }
    /* .basket {
        min-width: auto;
    } */
}
@media only screen and (max-width : 380px) {
    .title {
        font-size: 40px;
    }
    .headerBlock {
        height: 105px;
    }
}