
.basketContent {
    width: 70%;
    margin: 30px auto 0;
    padding: 0 5px 0;
}
.basketItemBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 700;
    padding-bottom: 10px;
    border-bottom: 1px solid #B3B3B3;
}
.title {
    font-weight: 700;
    text-align: center;
    padding-bottom: 15px;
}

.basketItemBlock img {
    width: 140px;
    height: 193px;
    border: 1px solid #B2B2B2;
    border-radius: 5px;
    padding: 5px;
}
.itemPiece {
    display: flex;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    width: 120px;
    height: 40px;
   
}
.itemPiece .btn {
    width: 40px;
    font-size: 34px;
    line-height: 34px;
    text-align: center;
    color: #26A9E0;
    
}
.itemPiece .btn:hover {
    background: rgba(0, 0, 0, 0.1);
    
}

.itemPiece .itemPieceTablo {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    color: #000;
    font-size: 16px;
    line-height: 40px;
    width: 40px;
    text-align: center;
}
.price {
    font-size: 20px;
}
.pap {
    color: rgba(0, 0, 0, 0.3);
    font-size: 12px;
    font-weight: normal;
}
.del{text-align: right;padding-bottom: 17px;}
.del span{
    color: rgba(0, 0, 0, 0.3) ;
}

.del span:hover {
    color: #26A9E0;
}

@media only screen and (max-width : 400px) {
    .basketContent {
        width: 85%;
    }
    .basketItemBlock img {
        width: 100px;
        height: 136px;
        border: 1px solid #B2B2B2;
        border-radius: 5px;
        padding: 5px;
    }
}