.basketBlock {
    margin: 60px 0 0;
}
.hideBasket {
    color: rgba(0, 0, 0, 0.3);
    margin: -30px 0 10px 0;
}
.hideBasket:hover {
    color: rgba(0, 0, 0, 0.8);
}
.basketTitle {
    font-weight: 700;
}

.headerBasket {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: 0 auto;
    padding: 0 5px 7px;
    border-bottom: 1px solid #B3B3B3;
}

.headerBasket .delItem {
    color: rgba(0, 0, 0, 0.3);
}
.headerBasket .delItem:hover {
    color: black;
}

.emptyBasket {
    font-family: 'PT Sans',sans-serif;
    font-size: 16px;
    color: #808080;
    text-align: center;
    padding: 70px 0;
}
.footerBasketBlock {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    background: rgba(38, 169, 224, 0.1);
    width: 70%;
    margin: 0 auto;
    padding: 35px 5px;
    font-family: 'PT Sans',sans-serif;
}
.footerBasketBtn {
    font-size: 20px;
    background-color: #ED341C;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
    width: 220px;
    /* height: 60px; */
    text-align: center;
    padding: 18px 0;
}
.basketSumIm{
    padding-bottom: 10px;
}

