.itemContent {
    margin: 5px;
}
.itemContent:hover {
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
    transition: .3s;
}
.item {
    padding: 5px;
    border: 1px solid #B2B2B2;
    border-radius: 5px;
    text-align: center;
}
.item p {
    margin: 0;
    padding: 0;
    font-weight: 600;
}
.bottomItem {/* border: 1px solid black; */
    display: flex;
    justify-content: space-between;
    padding: 0 5px;
}

.photo {
    width: 180px;
    height: 250px;
    margin-bottom: 12px;
}

.priceBlock {
    border: 1px solid black;
    border-radius: 5px;
    padding: 5px;
}
.button {
    background: #26A9E0;
    color: #fff;
    padding: 5px;
    border-radius: 5px;
    /* cursor: pointer; */
    
}
.button:hover {
    background: #2298CA;
}
.buttonAdd {
    padding: 5px;
    border-radius: 5px;
    background: #8BC53F;
    color: #fff;
}