.navBlock {
    border-bottom: 1px solid #B2B2B2;
    background: #FFFFFF;
    color: #000000;
    font-weight: 700;
    font-family: 'PT Sans',sans-serif;
}
.navMenu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    border-left: 1px solid black;
    border-right: 1px solid black;
    width: 70%;
    margin: 5px auto;
    
    padding: 5px;
}

.navMenu span:hover {
    color: #26A9E0;
}